body {
    overflow-x: hidden;
    background-image: url("../img/corridor1.png");
    background-repeat: no-repeat;
}

:root {
    --cursorX: 50vw;
    --cursorY: 50vh;
    --animation-time: 5s;
}

:root:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: fixed;
    pointer-events: none;
    background: -webkit-radial-gradient( circle 15vmax at var(--cursorX) var(--cursorY), rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.99) 100%);
    background: -o-radial-gradient( circle 15vmax at var(--cursorX) var(--cursorY), rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.99) 100%);
    background: radial-gradient( circle 15vmax at var(--cursorX) var(--cursorY), rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.99) 100%)
}

#media-monster {
    height: 50vw;
}

.block1 {
    color: white;
    padding-bottom: 10px;
}

.mouse-coord {
    overflow-x: hidden;
}

.front-layer {
    height: 55vw;
    min-width: 110vw;
}

.monster-layer {
    pointer-events: all;
    position: absolute;
    left: 60%;
    top: 75%;
}

.monster-layer:hover {
    -webkit-animation: vanish 10s ease-in-out infinite;
    animation: vanish 10s ease-in-out infinite;
}

@keyframes flicker {
    0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
        opacity: .99;
        text-shadow: -1px #000;
    }
    20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
        opacity: 0.7;
        text-shadow: none;
    }
}

@-webkit-keyframes flicker {
    0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
        opacity: .99;
        text-shadow: -1px #000;
    }
    20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
        opacity: 0.7;
        text-shadow: none;
    }
}

@keyframes vanish {
    0% {
        opacity: 0;
        text-shadow: none;
    }
    100% {
        opacity: 0;
        text-shadow: none;
    }
}

@-webkit-keyframes vanish {
    0% {
        opacity: 0;
        text-shadow: none;
    }
    100% {
        opacity: 0;
        text-shadow: none;
    }
}

@media only screen and (max-width:500px) {
    #media-pref {
        max-width: 100%;
        height: 100vw;
    }
    .front-layer {
        max-width: 100%;
        height: 100vw;
    }
    .lights-layer {
        max-width: 5%;
        top: 45%;
        right: 27%;
    }
    .monster-layer {
        width: 20px;
        top: 70vw;
    }
}


